import { useState, useEffect } from 'react';
import { message, Button } from 'antd';
import { ethers } from 'ethers';
import { subAddress } from '../../utils/common.helper';
import './index.css';

const ConnectWallet = () => {
    const { ethereum } = window;
    const [currentAccount, setCurrentAccount] = useState("");

    const connectWallet = async () => {
        try {
            if (!ethereum) {
                message.error("Get MetaMask!");
                return;
            }

            const accounts = await ethereum.request({ method: "eth_requestAccounts" });
            await setConnectAccount(accounts[0]);
        } catch (error) {
            console.log(error)
        }
    }

    const setConnectAccount = async (account) => {
        const provider = new ethers.providers.Web3Provider(window.ethereum);
        const network = await provider.getNetwork();

        const currAccount = `${subAddress(account)} (${network.name.toUpperCase()} ${network.chainId})`;
        setCurrentAccount(currAccount);
    }

    useEffect(() => {
        const checkIfWalletIsConnected = async () => {
            try {
                if (!ethereum) {
                    message.error("Make sure you have metamask!");
                    return;
                }
    
                const accounts = await ethereum.request({ method: 'eth_accounts' });
                if (accounts.length > 0) {
                    await setConnectAccount(accounts[0]);
                }
            } catch (error) {
                console.log(error);
            }
        }

        if (ethereum) {
            ethereum.on('chainChanged', async () => {
                await checkIfWalletIsConnected();
            })

            ethereum.on('accountsChanged', async () => {
                await checkIfWalletIsConnected();
            })
        }
    }, [ethereum]);

    return (
        <div className='walletContainer'>
            {!currentAccount && (
                <Button type="primary" shape="round" size="small" onClick={connectWallet}>Connect Wallet</Button>
            )}

            {currentAccount && (
                <div className='Account'>{currentAccount}</div>
            )}
        </div>
    )
}

export default ConnectWallet;